import { useState } from 'react'

import Select from '@mui/material/Select'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'

import TokenIcon from './TokenIcon'

export default function SelectToken() {
  const [value, setValue] = useState('BUSD')
  const handleChange = (v: any) => setValue(v)
  return (
    <FormControl fullWidth>
      <Select
        value={value}
        label="token"
        onChange={handleChange}
      >
        <MenuItem value="USDT"><TokenIcon token="USDT" />USDT</MenuItem>
        <MenuItem value="USDC"><TokenIcon token="USDC" />USDC</MenuItem>
        <MenuItem value="BUSD"><TokenIcon token="BUSD" />BUSD</MenuItem>
      </Select>
    </FormControl>
  )
}
