import Avatar, { AvatarProps } from '@mui/material/Avatar'

/* eslint-disable import/no-webpack-loader-syntax */
import BUSD from '!!file-loader!assets/tokens/busd.png'
import USDC from '!!file-loader!assets/tokens/usdc.png'
import USDT from '!!file-loader!assets/tokens/usdt.png'
/* eslint-enable import/no-webpack-loader-syntax */

type Tokens = {
  [symbol: string]: string
}

const tokens: Tokens = {
  USDT,
  USDC,
  BUSD,
}

export default function TokenIcon({
  token,
  ...rest
}: { token: keyof Tokens } & AvatarProps) {
  const icon = tokens[token]
  return <Avatar src={icon} alt={token as string} variant="square" {...rest} />
}
